.event-tabs-container {
  background: linear-gradient(135deg, #0a0e27 0%, #1a0a3e 50%, #0d1a2e 100%);
  padding: 0;
  border-bottom: 3px solid rgba(0, 212, 255, 0.2);
}

.event-tabs {
  display: flex;
  gap: 0;
  overflow-x: auto;
  overflow-y: hidden;
  max-width: 100%;
  scrollbar-width: thin;
  scrollbar-color: rgba(0, 0, 0, 0.2) rgba(255, 255, 255, 0.1);
}

.event-tabs::-webkit-scrollbar {
  height: 4px;
}

.event-tabs::-webkit-scrollbar-track {
  background: rgba(255, 255, 255, 0.1);
}

.event-tabs::-webkit-scrollbar-thumb {
  background: rgba(0, 0, 0, 0.2);
  border-radius: 2px;
}

.event-tab {
  display: flex;
  align-items: center;
  gap: 10px;
  padding: 12px 18px;
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.05), rgba(255, 255, 255, 0.05));
  border: none;
  border-bottom: 4px solid transparent;
  color: rgba(255, 255, 255, 0.7);
  font-weight: 600;
  font-size: 13px;
  cursor: pointer;
  transition: all 0.3s ease;
  white-space: nowrap;
  flex-shrink: 0;
}

.event-tab:hover {
  background: linear-gradient(135deg, rgba(0, 0, 0, 0.1), rgba(255, 255, 255, 0.1));
  color: rgba(255, 255, 255, 0.9);
}

.event-tab.active {
  background: linear-gradient(135deg, rgba(255, 255, 255, 0.2), rgba(255, 255, 255, 0.1));
  border-bottom-color: #00d4ff;
  color: #fff;
  box-shadow: inset 0 -4px 8px rgba(0, 0, 0, 0.1), 0 0 10px rgba(0, 212, 255, 0.3);
}

.tab-icon {
  font-size: 24px;
  display: inline-block;
  line-height: 1;
  filter: drop-shadow(0 0 2px rgba(0, 212, 255, 0.4));
}

.tab-label {
  display: inline;
}

/* Mobile styles */
@media (max-width: 768px) {
  .event-tab {
    padding: 10px 14px;
    font-size: 11px;
    gap: 6px;
  }

  .tab-icon {
    font-size: 22px;
  }

  .tab-label {
    display: none;
  }
}

@media (max-width: 480px) {
  .event-tabs {
    gap: 0;
  }

  .event-tab {
    padding: 10px 12px;
    font-size: 10px;
    gap: 4px;
    flex: 1;
  }

  .tab-label {
    display: none;
  }

  .tab-icon {
    font-size: 16px;
  }
}
